import statistic from '../content/Statistic'
export default {
  props: ['userData','statisticList'],
  components: {
    statistic
  },
  data() {
    return {
      // imgURL: this.$store.state['imgURL']
    }
  },
  computed: {
    
  },
  methods: {
    toPersonalInfo () {
      this.$emit('toPersonalInfo');
    },
    toVip () {
      this.$emit('toVip');
    },
    toPage(text) {
      if(text == '优惠券(张)') {
        wx.navigateTo({
        url: '/pages/coupon/main'
      })
      }
    },
    login () {
      this.$emit('login');
    },
    async wechatAuth (e) {

      if (e.mp.detail.errMsg === 'getUserInfo:ok') {
        const userInfo = e.mp.detail.userInfo
        await this.$store.dispatch('updateUserinfo', userInfo)
      }

    }
  }
}
